export default {
	data() {
		return {
			indicatorDots: true, //播放指示器
			autoplay: true, //自动播放
			interval: 3000, //播放定时
			goodList: [], //保存请求过来的商品信息
		}
	},
	onLoad() {
		this.getGoodList();
	},
	methods: {
		// 获取商品信息
		getGoodList() {
			uni.request({
				url: this.$globalVariable.hostUrl + "/goods/goodsInfo",
				method: "POST",
				data: {
					goods_id: 3000
				},
				header: this.$globalVariable.requestHeader,
				success: res => {
					this.goodList = res.data.result;
					const {
						gallery
					} = res.data.result
					console.log("123res:", this.goodList);
				},
				fail: err => {

				}
			})
		}
	}
}
